// src/app/page.js
import Hero from '@/components/Hero';
import ServiceCard from '@/components/ServiceCard';
import Button from '@/components/Button';
import Link from 'next/link';

export const metadata = {
  title: 'Black Forge Consulting | Premium medizinisches Fachpersonal',
  description: 'Professionelle Vermittlung von qualifizierten Ärzten, Pflegekräften und medizinischem Fachpersonal für Ihre individuellen Bedürfnisse.',
}

export default function Home() {
  return (
    <>
      {/* Hero Section */}
      <Hero />
      
      {/* Feature Navigation */}
      <section className="bg-black-custom py-4 sticky-top shadow-sm" style={{ top: "76px", zIndex: 100 }}>
        <div className="container">
          <div className="row g-3 text-center">
            <div className="col-6 col-md-3">
              <Link href="#services" className="d-block text-decoration-none py-2">
                <div className="text-gold fs-4 mb-1">
                  <i className="bi bi-grid"></i>
                </div>
                <div className="text-white small fw-semibold">LEISTUNGEN</div>
              </Link>
            </div>
            <div className="col-6 col-md-3">
              <Link href="#about" className="d-block text-decoration-none py-2">
                <div className="text-gold fs-4 mb-1">
                  <i className="bi bi-info-circle"></i>
                </div>
                <div className="text-white small fw-semibold">ÜBER UNS</div>
              </Link>
            </div>
            <div className="col-6 col-md-3">
              <Link href="/leistungen" className="d-block text-decoration-none py-2">
                <div className="text-gold fs-4 mb-1">
                  <i className="bi bi-briefcase-medical"></i>
                </div>
                <div className="text-white small fw-semibold">FACHBEREICHE</div>
              </Link>
            </div>
            <div className="col-6 col-md-3">
              <Link href="/kontakt" className="d-block text-decoration-none py-2">
                <div className="text-gold fs-4 mb-1">
                  <i className="bi bi-envelope"></i>
                </div>
                <div className="text-white small fw-semibold">KONTAKT</div>
              </Link>
            </div>
          </div>
        </div>
      </section>
  
      {/* Services Section */}
      <section id="services" className="py-5 bg-dark">
        <div className="container py-5">
          <h2 className="text-center mb-5">
            <span className="pb-2 border-bottom border-gold">UNSERE LEISTUNGEN</span>
          </h2>
          
          <div className="row g-4">
            {/* Service 1 */}
            <div className="col-md-4">
              <ServiceCard 
                title="Ärztevermittlung"
                description="Fachärzte und Spezialisten für Kliniken, Praxen und medizinische Einrichtungen"
                icon={<i className="bi bi-hospital"></i>}
                link="/leistungen#aerztevermittlung"
              />
            </div>
            
            {/* Service 2 */}
            <div className="col-md-4">
              <ServiceCard 
                title="Pflegekraftvermittlung"
                description="Individuelle Betreuung durch qualifizierte Pflegekräfte"
                icon={<i className="bi bi-heart-pulse"></i>}
                link="/leistungen#pflegekraftvermittlung"
              />
            </div>
            
            {/* Service 3 */}
            <div className="col-md-4">
              <ServiceCard 
                title="Medizinische Fachkräfte"
                description="Spezialisiertes Fachpersonal für den Gesundheitsbereich"
                icon={<i className="bi bi-briefcase-medical"></i>}
                link="/leistungen#medizinische-fachkraefte"
              />
            </div>
          </div>
          
          <div className="text-center mt-5">
            <Button href="/leistungen" className="px-4 py-2">ALLE LEISTUNGEN ANSEHEN</Button>
          </div>
        </div>
      </section>
  
      {/* About Us Section */}
      <section id="about" className="py-5 bg-black-custom">
        <div className="container py-5">
          <div className="row align-items-center">
            <div className="col-lg-6 mb-4 mb-lg-0">
              <h2 className="mb-4">
                <span className="pb-2 border-bottom border-gold">ÜBER UNS</span>
              </h2>
              <p className="mb-4">
                Black Forge Consulting ist Ihr zuverlässiger Partner für die Vermittlung 
                qualifizierter medizinischer Fachkräfte. Wir verstehen die individuellen Bedürfnisse 
                unserer Kunden und bieten maßgeschneiderte Lösungen für optimale medizinische Versorgung 
                und Betreuung.
              </p>
              <Button href="/uber-uns" className="px-4 py-2">MEHR ÜBER UNS</Button>
            </div>
            <div className="col-lg-6">
              <div className="bg-dark-gray p-5 text-center rounded">
                <p className="text-muted">Bild: Unser Team</p>
              </div>
            </div>
          </div>
        </div>
      </section>
      
      {/* Testimonials */}
      <section className="py-5 bg-dark">
        <div className="container py-4">
          <h2 className="text-center mb-5">
            <span className="pb-2 border-bottom border-gold">STIMMEN UNSERER KUNDEN</span>
          </h2>
          <div className="row g-4">
            <div className="col-md-4">
              <div className="card bg-black-custom h-100 testimonial-card">
                <div className="card-body p-4">
                  <p className="card-text mb-4">
                    "Die Zusammenarbeit mit Black Forge Consulting hat uns geholfen, schnell qualifizierte Ärzte für unsere Klinik zu finden. Der Service war professionell und zuverlässig."
                  </p>
                  <div className="d-flex align-items-center">
                    <div className="rounded-circle bg-dark-gray" style={{width: '50px', height: '50px'}}></div>
                    <div className="ms-3">
                      <h5 className="mb-0 text-gold">Dr. Andreas Müller</h5>
                      <p className="mb-0 small text-secondary">Klinikleiter, Berlin</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4">
              <div className="card bg-black-custom h-100 testimonial-card">
                <div className="card-body p-4">
                  <p className="card-text mb-4">
                    "Wir waren beeindruckt von der schnellen und passgenauen Vermittlung von Pflegekräften. Black Forge hat unsere Anforderungen perfekt verstanden und umgesetzt."
                  </p>
                  <div className="d-flex align-items-center">
                    <div className="rounded-circle bg-dark-gray" style={{width: '50px', height: '50px'}}></div>
                    <div className="ms-3">
                      <h5 className="mb-0 text-gold">Maria Schmidt</h5>
                      <p className="mb-0 small text-secondary">Pflegedienstleitung, München</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4">
              <div className="card bg-black-custom h-100 testimonial-card">
                <div className="card-body p-4">
                  <p className="card-text mb-4">
                    "Als Arzt schätze ich die professionelle Vermittlung und die Berücksichtigung meiner beruflichen Präferenzen. Eine Partnerschaft, die ich gerne weiterempfehle."
                  </p>
                  <div className="d-flex align-items-center">
                    <div className="rounded-circle bg-dark-gray" style={{width: '50px', height: '50px'}}></div>
                    <div className="ms-3">
                      <h5 className="mb-0 text-gold">Dr. Sarah Weber</h5>
                      <p className="mb-0 small text-secondary">Fachärztin für Innere Medizin</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
  
      {/* CTA Section */}
      <section className="py-5 bg-black-custom">
        <div className="container py-5 text-center">
          <h2 className="mb-4">Bereit für professionelles medizinisches Personal?</h2>
          <p className="lead mb-4">Kontaktieren Sie uns für ein unverbindliches Beratungsgespräch.</p>
          <Button href="/kontakt" className="px-4 py-2">JETZT ANFRAGEN</Button>
        </div>
      </section>
    </>
  );
}